import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '@/views/HomePage.vue'; // Import the HomePage component
import AboutPage from '@/views/AboutPage.vue'; // Import the AboutPage component

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomePage, // Use the imported component
  },
  {
    path: '/about',
    name: 'About',
    component: AboutPage, // Use the imported component
  },
  // ... other routes if needed
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;

